:global .youtube-sort-panel {
  margin-bottom: 10px; }
  :global .youtube-sort-panel .youtube-sort-item {
    padding: 5px 0;
    width: calc(100% / 3);
    display: inline-block;
    font-size: 0.928rem;
    color: #707070;
    border: 1px solid #444444;
    background-color: #272727;
    cursor: pointer;
    text-align: center; }
    :global .youtube-sort-panel .youtube-sort-item:first-child {
      border-right: none;
      border-radius: 15px 0 0 15px; }
    :global .youtube-sort-panel .youtube-sort-item:last-child {
      border-left: none;
      border-radius: 0 15px 15px 0; }
    :global .youtube-sort-panel .youtube-sort-item.selected {
      cursor: default;
      color: #cfcfcf;
      background-color: #1F1F1F; }
    :global .youtube-sort-panel .youtube-sort-item:hover {
      color: #cfcfcf;
      background-color: #1F1F1F; }



