:global .youtube-list-item {
  margin: 0 5px 5px 0;
  cursor: pointer;
  font-size: 0.928rem;
  color: #707070;
  padding: 5px;
  border-radius: 3px;
  border: 1px solid #444444;
  background-color: #272727; }
  :global .youtube-list-item.selected {
    cursor: default;
    color: #cfcfcf;
    background-color: #1F1F1F; }
  :global .youtube-list-item:hover {
    color: #cfcfcf;
    background-color: #1F1F1F; }



